@font-face {
    font-family: 'Sans Forgetica';
    src: url('../fonts/SansForgetica-Regular.eot');
    src: url('../fonts/SansForgetica-Regular.eot?#iefix') format('embedded-opentype'),
         url('../fonts/SansForgetica-Regular.woff2') format('woff2'),
         url('../fonts/SansForgetica-Regular.woff') format('woff'),
         url('../fonts/SansForgetica-Regular.ttf') format('truetype'),
         url('../fonts/SansForgetica-Regular.svg#SansForgetica-Regular') format('svg');
    font-weight: normal;
    font-style: normal;
}